.screen {
   display: none;
   padding: 25px 0 0;
   border-top: 1px solid $color-11;
   &__slider {
      &-img {
         @include cover; }
      &-item {}
      &-title {}
      &-descr {}
      &-btn {} } }

.benefits {
   padding: 60px 0;
   text-align: center;
   &__title {
      margin-bottom: 35px; }
   &__row {
      padding-bottom: 30px; }
   &__item {
      flex: 0 1 33.33333%;
      padding: 0 15px;
      &:nth-child(3) ~ & {
         padding: 30px 15px 0; }
      &-icon {
         margin: 0 auto;
         &-wrap {
            margin-bottom: 15px; }
         &_1 {
            @include sprite('pig-money'); }
         &_2 {
            @include sprite('team'); }
         &_3 {
            @include sprite('money'); }
         &_4 {
            @include sprite('credit-card'); }
         &_5 {
            @include sprite('stove'); }
         &_6 {
            @include sprite('guarantee'); } }
      &-title {
         margin-bottom: 15px;
         @include f('os700', 14, 32);
         text-transform: uppercase;
         span {
            color: #0057ae; } }
      &-descr {
         color: $color-2;
         font-size: 14px;
         line-height: 20px; } } }

.team-poster {
   padding: 40px 0;
   text-align: center;
   background: rgba($color-4, .6);
   &__container {}
   &__block {
      padding: 30px 22px;
      box-shadow: 0 0 13px 2px rgba(0, 0, 0, 0.2);
      background: $white; }
   &__title {}
   &__sub-title {}
   &__img {
      @include coverdiv;
      @include cover;
      &-wrap {
         margin: 20px 0;
         @include cover-img;
         @include ratio(1127, 591); } }
   &__bot-text {}
   &__descr {
      margin-bottom: 10px;
      color: $color-2;
      font-size: 16px;
      line-height: 20px; } }

.steps {
   padding: 60px 0;
   &__row {
      align-items: flex-start; }
   &__title {
      max-width: 700px;
      margin: 0 auto 30px;
      line-height: 49px; }
   &__item {
      position: relative;
      flex: 0 1 33.33333%;
      padding: 0 15px;
      height: 367px;
      background: url("../img/bg/steps-item_bg.png") no-repeat 50% 50% / 367px 234px;
      @include vertical;
      text-align: left;
      &:nth-child(3) ~ & {
         margin-top: 30px; }
      &:not(:nth-child(3n))::after {
         position: absolute;
         top: 0;
         right: 0;
         @include sprite('arr-dotted');
         content: ''; }
      &-icon {
         position: relative;
         z-index: 1;
         display: inline-block;
         vertical-align: middle;
         &-wrap {
            position: absolute;
            top: 0;
            left: 0;
            width: 68px;
            height: 68px;
            box-shadow: 5px 5px 13px 2px rgba(0, 0, 0, 0.15);
            background-color: $white;
            border-radius: 50%;
            @include vertical;
            &::after {
               @include vcenter;
               width: 54px;
               height: 54px;
               box-shadow: 5px 5px 9px 1px rgba(0, 0, 0, 0.13);
               background-color: #0057ae;
               border-radius: 50%;
               content: ''; } }
         &_1 {
            @include sprite('operator'); }
         &_2 {
            @include sprite('list'); }
         &_3 {
            @include sprite('3d-modeling'); }
         &_4 {
            @include sprite('manufacturing'); }
         &_5 {
            @include sprite('shipped'); }
         &_6 {
            @include sprite('kitchen'); } }
      &-text {
         display: inline-block;
         vertical-align: middle;
         padding: 0 40px; }
      &-title {
         margin-bottom: 10px;
         text-transform: uppercase;
         @include f('os700', 13, 14); }
      &-descr {
         font-size: 12px;
         line-height: 16.53px; } } }

.request {
   padding: 0 0 88px;
   @include cover;
   &__form {
      max-width: 398px;
      padding: 20px 15px;
      background: $white;
      box-shadow: 0 0 13px 2px rgba(0, 0, 0, 0.2);
      &-title {
         margin-bottom: 15px;
         padding-bottom: 10px;
         line-height: 34px;
         font-size: 22px;
         text-align: left;
         border-bottom: 1px solid rgba($color-1, .3); }
      &-row {}

      &-field {
         padding: 0 25px; }
      &-send {} } }

.anket {
   position: relative;
   padding: 60px 0;
   &__block {
      position: relative;
      max-width: 830px;
      margin: 0 auto;
      padding: 30px 50px;
      border: 6px solid rgba($color-2, .4);
      background: $white;
      z-index: 1;
      &::after {
         position: absolute;
         bottom: 0;
         right: 0;
         z-index: 1;
         @include sprite('promo-zero');
         content: ''; }
      &-inner {
         padding: 40px;
         box-shadow: 0 0 13px 2px rgba(0, 0, 0, 0.2); }
      &-top-text {
         margin-bottom: 30px; } }
   &__form {
      &-text {
         margin-bottom: 30px;
         padding-bottom: 15px;
         border-bottom: 1px solid rgba($color-1, .3);
         text-align: center; }
      &-row {
         margin: 0 -25px 25px; }
      &-field {
         flex: 0 1 50%;
         padding: 0 25px; }
      &-send {
         max-width: 340px;
         margin: 0 auto;
         text-align: center; } }

   &__bg {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      &_l {
         left: 0;
         width: 409px;
         height: 536px; }
      &_r {
         right: 0;
         width: 407px;
         height: 544px; } } }

.promo {
   padding: 70px;
   @include cover;
   &__container {
      display: flex; }

   &__video-block {
      width: calc(100% - 437px); }
   &__get-video {}
   &__top-text {
      margin-bottom: 10px;
      border-bottom: 1px solid rgba($color-1, .5); }
   &__title {
      margin-bottom: 10px; }
   &__sub-title {}
   &__descr {
      margin-bottom: 10px;
      font-size: 17px;
      line-height: 22px; }
   &__video {
      &-wrap {
         position: relative;
         width: 722px;
         height: 407px;
         &::after {
            position: absolute;
            left: 0;
            bottom: -75px;
            width: 971px;
            height: 75px;
            background: url("../img/other/block-shadow.png");
            content: ''; }
         iframe {
            @include coverdiv;
            width: 100%;
            height: 100%; } }
      &-text {
         position: absolute;
         top: 30px;
         left: 30px;
         z-index: 1;
         ul {
            li {
               position: relative;
               padding-left: 20px;
               @include f('os700', 22, 28);
               &::before {
                  position: absolute;
                  top: 5px;
                  left: 0;
                  width: 10px;
                  height: 10px;
                  border-radius: 50%;
                  background-color: #0057ae;
                  content: ''; } } } } } }
