  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #000;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.out {
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

@media only screen and (max-width: 1199px) {
  .row {
    justify-content: center;
    margin: 0 -10px;
  }
}

@media only screen and (max-width: 767px) {
  .row {
    flex-direction: column;
  }
}

.col {
  flex: 0 1 25%;
  padding: 0 15px;
}

.is-ie .col {
  max-width: 292px;
}

@media only screen and (max-width: 1199px) {
  .col {
    padding: 0 10px;
    flex: 0 1 33.33333%;
  }
}

@media only screen and (max-width: 767px) {
  .col {
    flex: 1 1 auto;
  }
}

@media only screen and (max-width: 767px) {
  .col:nth-child(1) ~ .col {
    padding: 30px 15px 0;
  }
}

.col:nth-child(4) ~ .col {
  padding: 30px 15px 0;
}

@media only screen and (max-width: 1199px) {
  .col:nth-child(3) ~ .col {
    padding: 20px 10px 0;
  }
}

.section {
  padding: 65px 0 80px;
}

@media only screen and (max-width: 767px) {
  .section {
    padding: 50px 0 60px;
  }
}

