}

.title, .title-md {
  font-family: "OpenSans-Extrabold", Arial, sans-serif;
  font-weight: normal;
  font-size: 34px;
  line-height: 46px;
  text-transform: uppercase;
  text-align: center;
}

.title span, .title-md span {
