  height: 41px;
}

.contact-block {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}

.contact-block__item {
  display: flex;
  flex: 0 1 33.33333%;
}

.contact-block__item-icon-wrap {
  flex: 0 0 25px;
  margin-right: 10px;
  border-right: 1px solid rgba(0, 87, 174, 0.6);
}

.contact-block__item-icon_loc {
  background-image: url("../img/sprite.png");
  width: 17px;
  height: 21px;
  background-position: -358px -72px;
}

.contact-block__item-icon_tel {
  background-image: url("../img/sprite.png");
  width: 13px;
  height: 23px;
  background-position: -358px -103px;
}

.contact-block__item-icon_clock {
  background-image: url("../img/sprite.png");
  width: 20px;
  height: 20px;
  background-position: -358px -42px;
}

.contact-block__item-text {
  font-size: 13px;
  color: #888888;
}

.get-video {
  width: 437px;
