.header {
  position: relative;
  z-index: 100;
}

.header__line {
  padding: 10px 0;
  text-align: center;
  background-color: #333333;
  letter-spacing: 0.38px;
  font-size: 15px;
  color: #fff;
}

.header__info {
  padding: 20px 0;
}

.header__info-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  flex: 0 0 198px;
}

.header__contact {
  flex: 1 1 auto;
}

.header__call-btn {
  min-width: 223px;
  padding: 11px 20px;
}

.header__nav {
  padding: 15px 0;
  border-top: 1px solid #e9e9e9;
}

@media only screen and (max-width: 767px) {
  .header__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    overflow: auto;
    opacity: 0;
    border: 0;
    padding: 0 0 60px;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #e8e8e8;
    text-align: center;
  }
}

.header__nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
}

.header__nav-list li a {
  font-family: "OpenSans-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #000;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 767px) {
  .header__nav-list li a {
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
  }
  .header__nav-list li a:hover {
    color: #0057ae;
  }
}

.header__nav-list li a:hover {
  color: #fff;
}

.header__nav-btn {
  display: none;
  border: 0;
  outline: 0;
  padding: 0;
  margin-left: 30px;
  z-index: 11;
}

@media only screen and (max-width: 767px) {
  .header__nav-btn {
    display: block;
  }
}

.header__nav-btn.is-active span {
  color: #0057ae;
}

.header__nav-btn-burger {
  margin: auto;
  width: 40px;
  height: 48px;
  cursor: pointer;
}

.header__nav-btn-lines {
  width: 40px;
  height: 3px;
  position: relative;
  transform: translateY(25px);
  background: white;
  transition: all .3s;
}

.header__nav-btn-lines:after, .header__nav-btn-lines:before {
  width: 40px;
  height: 3px;
}

.header__nav-btn.is-active .header__nav-btn-lines {
  background: rgba(255, 255, 255, 0);
}

.header__nav-btn-lines:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 10px;
  background: white;
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn-lines:after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  background: white;
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

